@import '../variables';

.header {
	border-bottom: 1px solid $gray-line;
	padding: 40px 0;

	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media (max-width: 997px) {
			flex-direction: column;
			.header__logo {
				margin-bottom: 20px;
			}
		}

		@media (max-width: 480px) {
			.header__logo {
				flex-wrap: wrap;
				justify-content: center;
				align-items: center;
				img {
					margin-bottom: 10px;
				}
			}
			h1,
			p {
				text-align: center;
			}
		}
	}

	&__logo {
		display: flex;

		img {
			margin-right: 15px;
		}

		h1 {
			color: #181818;
			font-size: 24px;
			letter-spacing: 1%;
			text-transform: uppercase;
			font-weight: 800;
		}

		p {
			color: #7b7b7b;
		}
	}
}
